  
<template>
  <section>
    <div v-if="loading">
      <h3>Loading...</h3>
    </div>

    <div v-if="error">
      <h1>{{error}}</h1>
      <router-link to="/">Go Back</router-link>
    </div>
    <div id="popup">
      <div class="grid">
        <div id="ranked" v-if="profileData.segments[0].stats.rankScore">
          <h3>{{profileData.segments[0].stats.rankScore.metadata.rankName}}</h3>
        <img :src="profileData.segments[0].stats.rankScore.metadata.iconUrl" alt class="rankIcon" />
        </div>
        <ul>
          <li v-if="profileData.segments[0].stats.level">
              <h4>Apex Level</h4>
              <p>
                {{profileData.segments[0].stats.level.displayValue}}
                <span>({{profileData.segments[0].stats.level.percentile}}%)</span>
              </p>
            </li>
            <li v-if="profileData.segments[0].stats.kills">
              <h4>Lifetime Kills</h4>
              <p>
                {{profileData.segments[0].stats.kills.displayValue}}
                <span>({{profileData.segments[0].stats.kills.percentile}}%)</span>
              </p>
            </li>
        </ul>
      </div>
    </div>

    <div v-if="profileData" class="container">
      <h1 class="gamertag">
        <img :src="profileData.platformInfo.avatarUrl" alt class="platform-avatar" />
        <span v-on:click="popUP" id="myPopup" title="click to show player stats">{{profileData.platformInfo.platformUserId}}</span>
      </h1>
      <div class="grid">
        <div>
          <img :src="profileData.segments[1].metadata.imageUrl" alt />
        </div>
        <div>
          <ul>
            <li>
              <h4>Selected Legend</h4>
              <p>{{profileData.metadata.activeLegendName}}</p>
            </li>
            <li v-if="profileData.segments[1].stats.kills">
              <h4>Legend kills</h4>
              <p>
                {{profileData.segments[1].stats.kills.displayValue}}
              </p>
            </li>
              <li v-if="profileData.segments[1].stats.damage">
              <h4>Legend Damage</h4>
              <p>
                {{profileData.segments[1].stats.damage.displayValue}}
              </p>
            </li>
            <li v-if="profileData.segments[1].stats.winningKills">
              <h4>Winning Kills</h4>
              <p>
                {{profileData.segments[1].stats.winningKills.displayValue}}
                <span>({{profileData.segments[1].stats.winningKills.percentile}}%)</span>
              </p>
            </li>
              <li v-if="profileData.segments[1].stats.season5Kills">
              <h4>Season 5 kills</h4>
              <p>
                {{profileData.segments[1].stats.season5Kills.displayValue}}
              </p>
            </li>
            <li v-if="profileData.segments[1].stats.season5Wins">
              <h4>Season 5 Wins</h4>
            </li>
            <li v-if="profileData.segments[1].stats.season4Wins">
              <h4>Season 4 Wins</h4>
            </li>
          </ul>
        </div>
      </div>
      <router-link to="/">Go Back</router-link>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  name: "Profile",
  data() {
    return {
      loading: false,
      error: null,
      profileData: null
    };
  },
  beforeCreate() {
    document.body.className = "body-bg-no-image";
  },
  async created() {
    this.loading = true;
    try {
      const res = await axios.get(
        `/api/v1/profile/${this.$route.params.platform}/${this.$route.params.gamertag}`
      );
      this.profileData = res.data.data;
      console.log(this.profileData);
      this.loading = false;
    } catch (err) {
      this.loading = false;
      this.error = err.response.data.message;
    }
  },
  methods: {
    popUP: function(event){
      console.log(event.target.tagName);
      var x = document.getElementById('popup');
      if (x.style.display === 'none') {
        x.style.display = 'block';
      } else {
        x.style.display = 'none';
      }
    }
  }
};
</script>

